    font-family: Poppins,'Times New Roman', Times, serif;
}
h1{
  font-size: 3rem;
    text-align: center;
}

.buttons{
  width: 87.5vw;
  display: flex;
  justify-content: space-evenly;
}

.startBtn,.play,.restartBtn,.random{
    border-radius: 10px;
    color: aliceblue;
    background-color: hsla(146, 90%, 42%, 0.596);
    font-size: 2rem;
    width: 15vw;
    text-align: center;
    transition: 0.15s ease-in-out ;
}
.buttons button:hover{
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 0px 0px 50px hsla(146, 90%, 42%, 0.596);
}
body {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    color: aliceblue;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.mainContainer{
  width: 80vw;
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container {
    border: none;

    font-size: 2rem;
  display: flex;
  gap: 10vw;
  justify-content: space-between;
}
.humanGrid {
    border: none;
  display: flex;
  width: 540px;
  height: 540px;
  flex-direction: column;
}
.computerGrid {
    border: none;
  display: flex;
  width: 540px;
  height: 540px;
  flex-direction: column;
}
.col {
    padding: 0;
    margin: 0;
  cursor: crosshair;
  transition: 0.1s ease-in-out;
}
.computerGrid .col:hover {
  background-color: hsla(187, 89%, 36%, 0.884);
  transform: scale(1.15);
}
.results{
  text-shadow: 1px 1px 5px aqua;
  text-align: center;
  font-size: 3rem;
